.SidebarOption{
    display: flex;
    color: gray;
    height: 40px;
    justify-content: center;
    padding: 40px 0px;
    cursor: pointer;
    transition: 200ms color ease-in-out;
}

.SidebarOption:hover{
    color: white;
}

.sidebarOption_icon{
    margin-right:20px ;
}

.sidebarOption_link , .sidebarOption_link:hover , .sidebarOption_link:focus{
    text-decoration: none;
    color: inherit;
}