.adminPage{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height:100vh;
    min-width: 600px;
}


.sidebar_logo{
    margin-top: 20px;
    text-align: center;
}

.admin_left{
    -webkit-flex: 0.18 1;
            flex: 0.18 1;
    background-color:black;
    color: white;
    padding-left: 10px;
    padding-right: 10px;
    min-width: 230px;
}

.admin_left > hr{
        border: 2px solid gray;
        width: 90%;
        margin: 10px auto;
}

.admin_right > hr{
    border: 2px solid white;
    width: 90%;
    margin: 20px auto;
}

.admin_right{
    -webkit-flex: 0.82 1;
            flex: 0.82 1;
    background: linear-gradient(#22c1c3,#fdbb2d);
    padding: 30px;
    height: 100vh;
    -webkit-flex: 0.88 1;
            flex: 0.88 1;
    color: white;
    overflow-y: overlay;
}

.admin_right::-webkit-scrollbar{
    display: none;
}



.body_info{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    padding: 10px;
}

.body_infoText{
    font-size: 14px;
}

.body_infoText > h2{
    font-size: 48px;
    margin-bottom: 10px;
}

.Admin_header{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 30px;
}

.header_left{
    -webkit-flex: 0.5 1;
            flex: 0.5 1;
    min-width: 100px;
    padding:10px;
}

.header_right{
    display: -webkit-flex;
    display: flex;
}

.header_right > h4{
    margin-left: 10px;
}
.SidebarOption{
    display: -webkit-flex;
    display: flex;
    color: gray;
    height: 40px;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 40px 0px;
    cursor: pointer;
    transition: 200ms color ease-in-out;
}

.SidebarOption:hover{
    color: white;
}

.sidebarOption_icon{
    margin-right:20px ;
}

.sidebarOption_link , .sidebarOption_link:hover , .sidebarOption_link:focus{
    text-decoration: none;
    color: inherit;
}
Body {
  all:"unset";
  margin: 0 ;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif ;
  -webkit-font-smoothing: "antialiased" ;
  -moz-osx-font-smoothing: "grayscale"  ;
}

Code {
  all:"unset";
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

