.adminPage{
    display: flex;
    width: 100%;
    height:100vh;
    min-width: 600px;
}


.sidebar_logo{
    margin-top: 20px;
    text-align: center;
}

.admin_left{
    flex: 0.18;
    background-color:black;
    color: white;
    padding-left: 10px;
    padding-right: 10px;
    min-width: 230px;
}

.admin_left > hr{
        border: 2px solid gray;
        width: 90%;
        margin: 10px auto;
}

.admin_right > hr{
    border: 2px solid white;
    width: 90%;
    margin: 20px auto;
}

.admin_right{
    flex: 0.82;
    background: linear-gradient(#22c1c3,#fdbb2d);
    padding: 30px;
    height: 100vh;
    flex: 0.88;
    color: white;
    overflow-y: overlay;
}

.admin_right::-webkit-scrollbar{
    display: none;
}



.body_info{
    display: flex;
    align-items: flex-end;
    padding: 10px;
}

.body_infoText{
    font-size: 14px;
}

.body_infoText > h2{
    font-size: 48px;
    margin-bottom: 10px;
}

.Admin_header{
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}

.header_left{
    flex: 0.5;
    min-width: 100px;
    padding:10px;
}

.header_right{
    display: flex;
}

.header_right > h4{
    margin-left: 10px;
}